<template>
  <div class="hidden-print">
    <h1>Penjualan <span class="badge">Cetak Nota</span></h1>
    <hr>
  </div>

  <section id="nota">
    <div class="text-center">
      <!-- <h3>Toko al Barokah</h3> -->
      <img class="logo-aplikasi" src="../../assets/logo-albarokah-small.png" alt="Logo">
      <hr>
      <h3>Nota Penjualan</h3>
    </div>

    <table class="meta-info">
      <tr>
        <td>Kode</td>
        <td>{{ sale._id }}</td>
      </tr>
      <tr>
        <td>Tanggal</td>
        <td>{{ sale.created | tanggal }}</td>
      </tr>
    </table>

    <table class="table table-bordered table-condensed">
      <thead>
        <tr>
          <th>Barang</th>
          <th>Harga</th>
          <th class="text-center">Qty</th>
          <th>Subtotal</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in sale.purchased">
          <td>{{ item.barang }}</td>
          <td>{{ item.harga | money }}</td>
          <td class="text-center">{{ item.qty }}</td>
          <td>{{ item.subtotal | money }}</td>
        </tr>
      </tbody>
    </table>

    <table class="meta-info">
      <tr>
        <td>Total</td>
        <td>{{ sale.total | money }}</td>
      </tr>
      <tr>
        <td>Dibayar</td>
        <td>{{ sale.dibayar | money }}</td>
      </tr>
      <tr>
        <td>Kembalian</td>
        <td>{{ sale.kembalian | money }}</td>
      </tr>
      <tr>
        <td>Kasir</td>
        <td>{{ kasir }}</td>
      </tr>
    </table>
  </section>

  <div class="hidden-print">
    <hr>
    <button @click="printNota()" class="btn btn-lg btn-success pull-right"><span class="glyphicon glyphicon-print" aria-hidden="true"></span> Print Nota</button>
  </div>

</template>
<script>
  import accounting from 'accounting';
  import moment from 'moment';
  export default {
    data() {
      return {
        sale: {},
        kasir: '',
      };
    },

    methods: {
      loadData() {
        const id = this.$route.params.id;
        this.$http.get(`api/sales/${id}`).then(
          (response) => {
            this.$set('sale', response.data[0]);
          },
          (response) => {
            // error
          }
        );
      },

      printNota() {
        // document.body.style.zoom = 0.5;
        window.print();
      },

      getKasir() {
        this.kasir = localStorage.getItem('kasir');
      },
    },

    filters: {
      money(number) {
        return accounting.formatMoney(number, 'Rp ', 0, '.', ',');
      },
      tanggal(str) {
        return moment.unix(str).format('dddd, D MMMM YYYY - HH:mm');
      },
    },

    route: {
      activate() {
        this.loadData();
        this.getKasir();
      },
    },
  };
</script>
<style scoped>
img.logo-aplikasi {
  width: 270px;
}

h3 {
  text-transform: uppercase;
}

table.meta-info {
  margin: 15px 0;
}

table.meta-info tr td:first-of-type {
  width: 100px;
  text-align: left;
  font-weight: bold;
}

@media print {
  * {
    color: #000 !important;
    text-shadow: none !important;
    background: transparent !important;
    box-shadow: none !important;
  }

  body {
    width: 100%;
    margin: 0;
    padding: 0;
    font-size: 85%;
  }

  .table {
    border-collapse: collapse !important;
  }
  .table-bordered th,
  .table-bordered td {
    border: 1px solid #999 !important;
  }

  img.logo-aplikasi {
    width: 200px;
  }

  section#nota {
    position: absolute;
    top: 15px;
    width: 95%;
    transform: scale(.95);
  }
}
</style>


