export default {
  user: {
    authenticated: false,
    name: '',
  },

  login(kasir) {
    localStorage.setItem('kasir', kasir);
    this.checkAuth();
  },

  logout() {
    localStorage.removeItem('kasir');
    this.user.authenticated = false;
  },

  checkAuth() {
    const kasir = localStorage.getItem('kasir');

    if (kasir) {
      this.user.authenticated = true;
      this.user.name = kasir;
    }
  },
};



