import auth from '../auth';

import WelcomeComponent from '../components/Welcome.vue';
import PricesIndexComponent from '../components/prices/index.vue';
import PricesCreateComponent from '../components/prices/create.vue';
import PricesEditComponent from '../components/prices/edit.vue';
import SalesIndexComponent from '../components/sales/index.vue';
import SalesShowComponent from '../components/sales/show.vue';
import ReportsIndexComponent from '../components/reports/index.vue';

auth.checkAuth();

const routes = {
  '/': {
    component: WelcomeComponent,
  },
  '/prices': {
    auth: true,
    component: PricesIndexComponent,
  },
  '/prices/create': {
    auth: true,
    component: PricesCreateComponent,
  },
  '/prices/edit/:id': {
    auth: true,
    name: 'edit-price',
    component: PricesEditComponent,
  },
  '/sales': {
    auth: true,
    component: SalesIndexComponent,
  },
  '/sales/:id': {
    auth: true,
    name: 'show-sale',
    component: SalesShowComponent,
  },
  '/reports': {
    auth: true,
    component: ReportsIndexComponent,
  },
};

export function configRouter(router) {
  router.map(routes);

  router.beforeEach((transition) => {
    if (transition.to.auth && ! auth.user.authenticated) {
      transition.redirect('/');
    }

    transition.next();
  });
}



