<template>
  <div class="text-center">
    <span class="glyphicon glyphicon-shopping-cart" aria-hidden="true"></span>
    <h3>Aplikasi Point of Sale</h3>
    <hr>
    <img src="../assets/logo-albarokah-small.png" alt="">
    <hr>
    <form v-if="!authenticated" @submit.prevent="login()" class="form-inline">
      <input type="text" class="form-control" placeholder="Nama Kasir" v-model="kasir">
      <button class="btn btn-success">Masuk</button>
    </form>
    <button v-else class="btn btn-success" @click="logout()">Logout</button>
  </div>
</template>
<script>
  import auth from '../auth';
  export default {
    data() {
      return {
        kasir: '',
        authenticated: false,
      };
    },
    methods: {
      login() {
        if (this.kasir.length > 0) {
          auth.login(this.kasir);
          this.$set('authenticated', true);
          // this.kasir = '';
        }
      },
      logout() {
        auth.logout();
        this.$set('authenticated', false);
      },
      check() {
        const kasir = localStorage.getItem('kasir');
        if (kasir) {
          this.$set('authenticated', true);
        }
      },
    },
    route: {
      activate() {
        this.check();
      },
    },
  };
</script>
<style scoped>
  span.glyphicon {
    font-size: 60px;
    margin-top: 25px;
  }
</style>



