<template>
  <h1>Laporan</h1>
  <hr>

  <table class="table table-bordered">
    <thead>
      <tr>
        <th>Kode</th>
        <th>Tanggal</th>
        <th>Jml Barang</th>
        <th>Total Harga</th>
        <td>Dibayar</td>
      </tr>
    </thead>
    <tbody>
      <tr v-for="sale in sales">
        <td><a v-link="{ name: 'show-sale', params: { id: sale._id } }">{{ sale._id }}</a></td>
        <td>{{ sale.created | tanggal }}</td>
        <td>{{ sale.purchased.length }}</td>
        <td>{{ sale.total | money }}</td>
        <td>{{ sale.dibayar | money }}</td>
      </tr>
    </tbody>
  </table>
</template>
<script>
  import accounting from 'accounting';
  import moment from 'moment';
  export default {
    data() {
      return {
        sales: [],
      };
    },

    methods: {
      loadData() {
        this.$http.get('api/sales').then(
          (response) => {
            this.$set('sales', response.data);
          },
          (response) => {
            // error
          }
        );
      },
    },

    filters: {
      money(number) {
        return accounting.formatMoney(number, 'Rp ', 0, '.', ',');
      },
      tanggal(str) {
        return moment.unix(str).format('dddd, D MMMM YYYY - HH:mm');
      },
    },

    route: {
      activate() {
        this.loadData();
      },
    },
  };
</script>


