<template>
  <h1>Penjualan</h1>
  <hr>

  <p class="pilih-barang">
    <typeahead :data="barang" :on-hit="itemSelected" placeholder="Pilih Barang..."></typeahead>
  </p>

  <table class="table table-bordered">
    <thead>
      <tr>
        <th>Barang</th>
        <th>Harga Satuan</th>
        <th>Qty</th>
        <th>Subtotal</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="item in purchased">
        <td>{{ item.barang }}</td>
        <td>{{ item.harga | money }}</td>
        <td class="col-md-1 text-center">
          <input type="text" class="form-control" v-model="item.qty" @keyup="qtyChanged(item)" number>
        </td>
        <td>{{ item.subtotal | money }}</td>
      </tr>
    </tbody>
  </table>

  <h3>Total Belanja: {{ total | money }}</h3>
  <hr>
  <form class="form-horizontal" @submit.prevent="cetakNota()">
    <div class="form-group">
      <label class="control-label col-sm-3">Dibayar</label>
      <div class="col-sm-3">
        <input v-model="dibayar" type="text" class="form-control" placeholder="Uang dibayarkan">
      </div>
    </div>

    <div class="form-group">
      <label class="control-label col-sm-3">Kembalian</label>
      <div class="col-sm-3">
        <input type="text" class="form-control" v-model="kembalian" disabled>
      </div>
    </div>

    <div class="form-group">
      <div class="col-sm-3 col-sm-offset-3">
        <button class="btn btn-success" :disabled="total === 0">Buat Nota</button>
      </div>
    </div>
  </form>
</template>
<script>
  import accounting from 'accounting';
  import { typeahead } from 'vue-strap';
  export default {
    computed: {
      total() {
        const totalarr = this.purchased.map((item) => item.subtotal);
        return totalarr.reduce((a, b) => a + b, 0);
      },

      kembalian() {
        return this.dibayar - this.total;
        // if (this.dibayar > 0) {
        //   return this.dibayar - this.total;
        // }

        // return 0;
      },
    },

    data() {
      return {
        barang: [],
        prices: [],
        purchased: [],
        dibayar: 0,
      };
    },

    methods: {
      loadData() {
        this.$http.get('api/prices').then(
          (response) => {
            const items = response.data.map((item) => item.barang);
            this.$set('barang', items);
            this.$set('prices', response.data);
          },
          (response) => {
            // error
          }
        );
      },

      itemSelected(item, vm) {
        const that = vm;
        that.reset();
        // that.query = item;

        const filtered = this.prices.find((p) => p.barang === item);
        const qty = 1;
        const subtotal = qty * filtered.harga.jual;
        const harga = filtered.harga.jual;
        const barang = filtered.barang;

        const newPurchased = Object.assign({}, { qty, subtotal, harga, barang });

        this.purchased.push(newPurchased);
      },

      qtyChanged(item) {
        const idx = this.purchased.indexOf(item);
        const newItem = item;
        newItem.subtotal = item.qty * item.harga;
        this.purchased.$set(idx, newItem);
      },

      cetakNota() {
        const params = {
          purchased: this.purchased,
          total: this.total,
          dibayar: parseInt(this.dibayar, 10),
          kembalian: this.kembalian,
        };

        this.$http.post('api/sales', params).then(
          (response) => {
            const id = response.data._id;
            this.$route.router.go({ name: 'show-sale', params: { id } });
          },
          (response) => {
            // error
          }
        );
      },
    },

    components: {
      typeahead,
    },

    filters: {
      money(number) {
        return accounting.formatMoney(number, 'Rp ', 0, '.', ',');
      },
    },

    route: {
      activate() {
        this.loadData();
      },
    },
  };
</script>


