<template>
  <h1>Barang dan Harga <span class="badge">Edit Data</span></h1>
  <hr>

  <div v-if="errors.length" class="alert alert-danger">
    <ul v-for="error in errors">
      <li>{{ error.msg }}</li>
    </ul>
  </div>

  <form @submit.prevent="formSubmit()" class="form-horizontal" novalidate>
    <div class="form-group">
      <label class="control-label col-md-3">Barang</label>
      <div class="col-md-5">
        <input type="text" class="form-control" v-model="price.barang" required>
      </div>
    </div>

    <div class="form-group">
      <label class="control-label col-md-3">Harga Pokok</label>
      <div class="col-md-3">
        <input type="number" min="0" step="1" class="form-control" v-model="price.harga.pokok" required>
      </div>
    </div>

    <div class="form-group">
      <label class="control-label col-md-3">Harga Jual</label>
      <div class="col-md-3">
        <input type="number" min="0" step="1" class="form-control" v-model="price.harga.jual" required>
      </div>
    </div>

    <div class="form-group">
      <div class="col-md-5 col-md-offset-3">
        <button class="btn btn-success">Submit</button>
      </div>
    </div>
  </form>
</template>
<script>
  export default {
    data() {
      return {
        price: {
          barang: '',
          harga: {
            pokok: 0,
            jual: 0,
          },
        },
        errors: [],
      };
    },
    methods: {
      loadData() {
        const id = this.$route.params.id;
        this.$http.get(`api/prices/${id}`).then(
          (response) => {
            this.$set('price', response.data);
          },
          (response) => {
            // error
          }
        );
      },

      formSubmit() {
        const id = this.$route.params.id;
        this.$http.put(`api/prices/${id}`, this.price).then(
          (response) => {
            this.$route.router.go('/prices');
          },
          (response) => {
            this.$set('errors', response.data);
          }
        );
      },
    },

    route: {
      activate() {
        this.loadData();
      },
    },
  };
</script>


