<template>
  <p class="pull-right"><a class="btn btn-primary" v-link="'/prices/create'">Tambahkan Barang</a></p>
  <h1>Barang dan Harga</h1>
  <hr>

  <p>
    <input type="text" class="form-control" v-model="search" placeholder="Cari barang...">
  </p>

  <table class="table table-bordered">
    <thead>
      <tr>
        <th>Barang</th>
        <th>Harga Pokok</th>
        <th>Harga Jual</th>
        <th></th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="item in prices | filterBy search">
        <td>{{ item.barang }}</td>
        <td>{{ item.harga.pokok | money }}</td>
        <td>{{ item.harga.jual | money }}</td>
        <td class="text-center">
          <a v-link="{name: 'edit-price', params: {id: item._id}}" class="btn btn-sm btn-primary"><span class="glyphicon glyphicon-pencil" aria-hidden="true"></span> Edit</a>
          <button class="btn btn-sm btn-danger" @click="confirmRemoveItem(item)"><span class="glyphicon glyphicon-trash" aria-hidden="true"></span> Delete</button>
        </td>
      </tr>
    </tbody>
  </table>

  <!-- Modal -->
  <div class="modal fade" id="konfirmasi" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
    <div class="modal-dialog" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
          <h4 class="modal-title" id="myModalLabel">Konfirmasi Hapus</h4>
        </div>
        <div class="modal-body">
          Kamu yakin hapus data ini?
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-default" data-dismiss="modal">Batalkan</button>
          <button type="button" class="btn btn-danger" @click="removeItem()">Yakin Hapus</button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import accounting from 'accounting';
  export default {
    data() {
      return {
        prices: [],
        search: '',
        selected: {},
      };
    },

    methods: {
      loadData() {
        this.$http.get('api/prices').then(
          (response) => {
            this.$set('prices', response.data);
          },
          (response) => {
            // error
          }
        );
      },

      confirmRemoveItem(item) {
        this.selected = item;
        $('#konfirmasi').modal();
      },

      removeItem() {
        $('#konfirmasi').modal('hide');
        this.$http.delete('api/prices', { id: this.selected._id }).then(
          (response) => {
            this.prices.$remove(this.selected);
            this.$set('selected', {});
          },
          (response) => {
            // error
          }
        );
      },
    },

    filters: {
      money(number) {
        return accounting.formatMoney(number, 'Rp ', 0, '.', ',');
      },
    },

    route: {
      activate() {
        this.loadData();
      },
    },
  };
</script>


