import '../node_modules/bootstrap/dist/css/bootstrap.css';
import 'bootstrap';

import Vue from 'vue';
import VueRouter from 'vue-router';
import VueResource from 'vue-resource';
import App from './App';

import { configRouter } from './routes';

// config for urls
import urls from '../config/urls';

import AppNavbar from './components/partials/Nav.vue';

Vue.use(VueRouter);
Vue.use(VueResource);

const router = new VueRouter();
const params = {
  replace: false,
  components: { App },
};

Vue.http.options.root = urls.api;

Vue.component('app-navbar', AppNavbar);

configRouter(router);

router.start(params, 'body');



